import React from "react";
import SpeciesGallery from "./gallery/speciesGallery.js";
import DashBoard from "./dashBoard.js";

function SideBar({ showSpecies, showDashBoard }) {
  if (showSpecies) return <SpeciesGallery />;
  else if (showDashBoard) return <DashBoard />;
  else
    return (
      <div
        id="side-bar"
        className="justify-center w-10 bg-primary duration-500"
      >
        <img src="./bloom.svg" className="h-10" />
      </div>
    );
}

export default SideBar;
